<template>
    <div class="element-wrapper padded-v" id="home-box-news">
        <div class="row">
            <div class="col-md-12">
                <div class="element-actions d-none d-sm-block">
                    <router-link class="btn " :to="{name:'program.index'}" title="mais programas" ><span class="text-capitalize text-success">mais programas</span></router-link>
                </div>
                <h6 class="element-header">
                    Novos Programas
                </h6>
            </div>
            <div class="col-md-6 col-lg-6 col-xl-12" v-if="programs.length">
                <program-box v-for="(program,index) in programs" :program="program" v-if="index<4"></program-box>
            </div>
            <div v-else class="col-md-12 col-xl-12">
                <p class="text-capitalize">ainda não há programas cadastrados</p>
            </div>
        </div>

    </div>
</template>
<script>
    import programBox from './ProgramBox'
    import programsMixins from '../../../../mixins/programs';

    export default {
        components: {programBox},
        mixins: [programsMixins],
        mounted(){
            this.getPrograms()
        }
    }
</script>