<template>
    <div class="element-box-tp">
        <div class="post-box">
            <div class="post-media" :style="`background-image: url(${program.cover})`"></div>
            <div class="post-content">
                <span class="badge badge-success">teste</span>
                <span class="badge badge-success">tag</span>
                <h6 class="post-title" :title="program.title">
                    <router-link :to="{name:'program.show',params:{id:program.id}}">{{program.title.substr(0,limit_string)}}
                        <span v-if="program.title.length>limit_string">...</span></router-link>
                </h6>
                <div class="post-dates text-muted small">
                    <strong><i class="fas fa-calendar-alt"></i> acaba em {{tsToPtBrString(program.date_end)}}</strong>
                </div>
                <div class="post-text" :title="program.description">{{program.description.substr(0,limit_string)}}...</div>
                <div class="post-foot">
                    <div class="post-tags">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import dateMixins from '../../../../mixins/dates'
    export default {
        mixins:[dateMixins],
        data() {
            return {
                limit_string: 45
            }
        },
        props: {
            program: {
                type: Object
            }
        }
    }
</script>
