<template>
    <div class="user-profile compact m-b" id="feature-news" v-if="news">
        <div class="up-head-w" :style="`backgroundImage:url(${news.image})`">
            <div class="up-main-info"></div>
        </div>
        <div class="up-contents">
                <div class="padded">
                    <span class="badge badge-success" v-for="tag in news.tags.split(',')">{{tag}}</span>
                    <h5 class="card-title">
                        <router-link :to="{name:'news.show',params:{id:news.id}}" class="title" :title="news.title">
                            {{news.title.substr(0,limit_title)}} <span v-if="news.title.length>limit_title">...</span>
                        </router-link>
                    </h5>
                    <p class="card-text text-grey" :title="news.resume">{{news.resume.substr(0,limit_resume)}} <span
                            v-if="news.resume.length>limit_resume">...</span></p>
                </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                limit_title: 40,
                limit_resume: 60,
            }
        },
        props: {
            news: {
                type: Object,
            }
        },
    }
</script>
<style lang="scss" scoped>
    #feature-news{
        max-height: 256px;
        &:hover{
            transform: translateY(-5px);
            box-shadow: 0px 5px 12px rgba(126, 142, 177, 0.2);
        }
        .user-profile {
            .up-contents {
                padding-top: 0;
            }
        }

        .up-main-info {
            padding-top: 21.5%;
        }

    }
</style>