<template>
    <div class="element-box-tp">
        <div class="post-box">
            <div class="post-media" :style="`background-image: url(${news.image})`"></div>
            <div class="post-content">
                <h6 class="post-title" :title="news.title">
                    <router-link :to="{name:'news.index',params:{id:news.id}}">{{news.title.substr(0,limit_title)}}
                        <span v-if="news.title.length>limit_title">...</span></router-link>
                </h6>
                <div class="post-text" :title="news.resume">{{news.resume.substr(0,limit_resume)}}<span
                        v-if="news.resume>limit_resume">...</span></div>
                <div class="post-foot">
                    <div class="post-tags">
                        <div class="badge badge-success" v-for="(tag,index) in news.tags.split(',')" v-if="index<4">
                            {{tag}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                limit_title: 35,
                limit_resume: 45
            }
        },
        props: {
            news: {
                type: Object
            }
        }
    }
</script>